/* HomePage.css */

/* General Styling */
.homepage-container {
    font-family: Arial, sans-serif;
    background-color: #f4f4f9;
    text-align: center;
    padding: 40px 20px;
  }
  
  /* Hero Section */
  .hero-section {
    background: linear-gradient(90deg, #4CAF50, #8E44AD);
    color: white;
    padding: 80px 20px;
    border-radius: 20px;
    max-width: 800px;
    margin: 0 auto 40px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  }
  
  .hero-section h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .hero-section p {
    font-size: 1.2rem;
    margin-bottom: 30px;
  }
  
  .cta-button {
    background-color: white;
    color: #4CAF50;
    padding: 12px 24px;
    font-size: 1rem;
    font-weight: bold;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .cta-button:hover {
    background-color: #e0e0e0;
  }
  
  /* Features Section */
  .features-section {
    max-width: 900px;
    margin: 40px auto;
  }
  
  .features-section h2 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: #333;
  }
  
  .features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .feature-item {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .feature-item h3 {
    color: #4CAF50;
    font-size: 1.5rem;
  }
  
  /* Pricing Section */
  .pricing-section {
    max-width: 800px;
    margin: 40px auto;
  }
  
  .pricing-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  }
  
  .pricing-item {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .pricing-item h3 {
    color: #8E44AD;
    font-size: 1.5rem;
  }
  
  /* Contact Section */
  .contact-section {
    margin-top: 40px;
    padding: 20px;
    background: #333;
    color: white;
    border-radius: 10px;
  }
  
  .contact-section h2 {
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  .contact-section p {
    font-size: 1.2rem;
  }
  